import { Component, OnInit } from '@angular/core';
import { BasePage } from '../../base/base.page';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Store } from '@ngxs/store';
import { UtilsService } from '../../@core/utils.service';

import { Observable } from 'rxjs';
import { NetworkState } from '../../@store/network/network.state';
import { UserPublicInfo } from 'src/app/networking/models/user-public-info.model';

@Component({
  selector: 'app-see-all-visits',
  templateUrl: './see-all-visits.page.html',
  styleUrls: ['./see-all-visits.page.scss'],
})
export class SeeAllVisitsPage extends BasePage implements OnInit {
  public profileVisitsList$: Observable<UserPublicInfo[]>;

  constructor(router: Router, location: Location, store: Store, utilsService: UtilsService) {
    super(location, router, store, utilsService);
  }

  ngOnInit() {
    this.profileVisitsList$ = this.store.select(NetworkState.getVisits);
  }
}
